.App {
  text-align: center;
}

.App-logo {
  height: 25vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.code-input {
  background: #4c5a72;
  border: 3px solid #9baccc;
  font-size: 50px;
  padding: 15px;
  text-align: center;
  color: #9baccc;
  font-weight: bold;
  text-transform: uppercase;
  outline: none;
}

.coord-output {
  background: #4c5a72;
  border: 3px solid #9baccc;
  font-size: 50px;
  padding: 15px;
  text-align: center;
  color: #9baccc;
  font-weight: bold;
  text-transform: uppercase;
  outline: none;
}

.code-input.invalid {
  border-color: #900;
  color: #900;
}

.code-input.valid {
  border-color: #090;
  color: #090;
}

.code-input::placeholder {
  color: #9baccc;
}

.btn-back {
  background: #4c5a72;
  border: 2px solid #9baccc;
  border-radius: 5px;
  font-size: 20px;
  margin: 20px 0 0 0;
  color: #9baccc;
  cursor: pointer;
}